    //ì•µì»¤íƒœê·¸ì— ë„£ì€ # = aíƒœê·¸ì˜ nameì†ì„±ê³¼ ê°™ì€ í•´ì‰¬ ì´ë¦„ì´ ë“¤ì–´ì˜¤ë©´ ê·¸ ìœ„ì¹˜ë¡œ ìŠ¤í¬ë¡¤ë§ë¨
